import React from "react";
import { useCMS } from "../../contexts/CMSContext";
import "./chuongtrinh.css";

const Page4ChuongTrinhSoTrungDang: React.FC = () => {
  const { displayData } = useCMS();
  const content = displayData.pages.page4.chuongTrinhSoTrungDangContent;

  if (!content) return null;

  const imgOnLeft = (content.imagePosition || 'left') === 'left';

  return (
    <section className="chuongtrinh-section">
      <div className="chuongtrinh-container">

        {/* IMAGE */}
        <div className="chuongtrinh-image" style={{ order: imgOnLeft ? 1 : 2 }}>
          <img
            src={content.image.src}
            alt={content.image.alt}
            className="chuongtrinh-image-content"
            data-aos="fade-up"
            onError={(e) => (e.currentTarget.style.display = 'none')}
          />
        </div>

        {/* RIGHT CONTENT */}
        <div className="chuongtrinh-content" style={{ order: imgOnLeft ? 2 : 1 }}>
          <div className="chuongtrinh-header">
            <h2 className="chuongtrinh-title" data-aos="fade-up">
              {content.title}
            </h2>
            <div
              className="chuongtrinh-underline"
              data-aos="fade-up"
              data-aos-delay="100"
            />
          </div>

          <div
            className="chuongtrinh-meta"
            data-aos="fade-up"
            data-aos-delay="200"
          >
            <p>{content.meta.duration}</p>
            {content.meta.schedule.map((line, i) => (
              <p key={i}>{line}</p>
            ))}
          </div>

          <div
            className="chuongtrinh-curriculum"
            data-aos="fade-up"
            data-aos-delay="300"
          >
            <div className="curriculum-grid">
              <div className="curriculum-column">
                <ul>
                  {content.curriculum.left.map((item, i) => (
                    <li key={i}>{item}</li>
                  ))}
                </ul>
              </div>

              <div className="curriculum-column">
                <ul>
                  {content.curriculum.right.map((item, i) => (
                    <li key={i}>{item}</li>
                  ))}
                </ul>
              </div>
            </div>
          </div>

        </div>
      </div>
    </section>
  );
};

export default Page4ChuongTrinhSoTrungDang;
