import React from "react"
import { RiDoubleQuotesL, RiDoubleQuotesR } from "react-icons/ri"
import { useCMS } from "../../contexts/CMSContext"
import "./record.css"

const Page4NienKhoa: React.FC = () => {
  const { displayData } = useCMS()
  const content = displayData.pages.page4.nienKhoaContent

  if (!content) return null

  return (
    <section className="page4-nienkhoa-section">
      {content.blocks.map((block, index) => (
        <React.Fragment key={block.id}>
          {/* ===== SCHEDULE BLOCK ===== */}
          {block.type === "schedule" && (
            <div className="page4-nienkhoa-block">
              <div
                className="page4-nienkhoa-content"
                style={{ order: (block.imagePosition || 'right') === 'right' ? 1 : 2 }}
              >
                <h2 className="page4-nienkhoa-title" data-aos="fade-up">
                  {block.title}
                </h2>
                <div className="page4-nienkhoa-underline" />

                <div className="page4-nienkhoa-course">
                  <h3
                    className="page4-nienkhoa-course-title"
                    data-aos="fade-up"
                  >
                    {block.schedules.heading}
                  </h3>

                  <div
                    className="page4-nienkhoa-schedule"
                    data-aos="fade-up"
                  >
                    {block.schedules.items.map(item => (
                      <div key={item.id} className="page4-schedule-item">
                        <strong>{item.dayLabel}</strong>
                        <div className="page4-schedule-details">
                          {item.details.map((line, i) => (
                            <div key={i}>{line}</div>
                          ))}
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
              </div>

              <div
                className="page4-nienkhoa-image"
                style={{ order: (block.imagePosition || 'right') === 'right' ? 2 : 1 }}
              >
                <picture>
                  <source
                    media="(max-width: 1000px)"
                    srcSet={block.imageMobile}
                  />
                  <img
                    src={block.imageDesktop}
                    alt={block.title}
                    data-aos="fade-up"
                  />
                </picture>

                {/* Mobile Overlay components */}
                <div className="page4-mobile-overlay" />
                <h2 className="page4-nienkhoa-mobile-title">
                  {block.title}
                </h2>
              </div>
            </div>
          )}

          {/* ===== COURSE BLOCK ===== */}
          {block.type === "course" && (
            <div className="page4-nienkhoa-block">
              <div
                className="page4-nienkhoa-content"
                style={{ order: (block.imagePosition || 'right') === 'right' ? 1 : 2 }}
              >
                <h2 className="page4-nienkhoa-title" data-aos="fade-up">
                  {block.title}
                </h2>
                <div className="page4-nienkhoa-underline" />

                {block.courses.map(course => (
                  <div
                    key={course.id}
                    className="page4-nienkhoa-course"
                  >
                    <h3
                      className="page4-nienkhoa-course-title2"
                      data-aos="fade-up"
                    >
                      {course.title} ›
                    </h3>
                    <p
                      className="page4-nienkhoa-paragraph"
                      data-aos="fade-up"
                    >
                      <RiDoubleQuotesL className="quote-left" />{'\u00A0'}{course.description}{'\u00A0'}<RiDoubleQuotesR className="quote-right" />
                    </p>
                  </div>
                ))}
              </div>

              <div
                className="page4-nienkhoa-image"
                style={{ order: (block.imagePosition || 'right') === 'right' ? 2 : 1 }}
              >
                <img
                  src={block.imageDesktop}
                  alt={block.title}
                  data-aos="fade-up"
                />

                {/* Mobile Overlay components */}
                <div className="page4-mobile-overlay" />
                <h2 className="page4-nienkhoa-mobile-title">
                  {block.title}
                </h2>
              </div>
            </div>
          )}

          {index === 0 && <div className="page4-mobile-pagination-line" />}
        </React.Fragment>
      ))}
    </section>
  )
}

export default Page4NienKhoa
